import { Component, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { Router } from '@angular/router';
import { DxBulletComponent, DxDataGridComponent } from 'devextreme-angular';
import { TestAgentsService } from './test-agents.service';
import { TestAgent } from './types';

@Component({
  selector: 'app-test-agents',
  templateUrl: './test-agents.component.html',
  styleUrls: ['./test-agents.component.scss']
})
export class TestAgentsComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @ViewChildren(DxBulletComponent) bullets: QueryList<DxBulletComponent>;

  dataSource: Array<TestAgent>;

  constructor(private testAgentsService: TestAgentsService,  private router: Router) {
    this.dataSource = testAgentsService.getTestAgents();
  }

  ngOnInit() {
    // this.dataSource = new MatTableDataSource(this.testAgentsService.getTestAgents());
    // this.dataSource.paginator = this.paginator;
    // this.dataSource.sort = this.sort;
  }

  sortByErrorSecond(value1: number, value2: number) {
    // Handling null values
    if (!value1 && value2) { return -1; }
    if (!value1 && !value2) { return 0; }
    if (value1 && !value2) { return 1; }

    // Determines whether two strings are equivalent in the current locale
    if (value1 < value2) { return -1; }
    if (value1 === value2) { return 0; }
    if (value1 > value2) { return 1; }
  }

  handleResized = (event) => {
    if (!this.bullets) {
      return;
    }

    this.bullets.forEach(bullet => {
      if (bullet.instance) {
        bullet.instance.render();
      }
    });
  }

  onRowClick(event: { data: { id: any; }; }) {
    this.router.navigate(['/dashboard'], { queryParams: { id: 'testagent', ta: event.data.id } });
  }

  getESToolTip(args) {
    return {
      html: 'Nice tooltip: ' + args.originalValue
    };
  }
}
