import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { ResizedEvent } from 'angular-resize-event';
import { DxChartComponent } from 'devextreme-angular';
import { Options } from 'devextreme/viz/chart';
import { DruidResults } from 'druid.d.ts';
import { debounce } from 'lodash';
import { CountryInfo, DataService, EnergyDescription } from 'src/app/core/services/data.service';

@Component({
  selector: 'app-graph4',
  templateUrl: './graph4.component.html',
  styleUrls: ['./graph4.component.scss']
})
export class Graph4Component implements OnInit, AfterViewInit {
  @ViewChild(DxChartComponent) chart: DxChartComponent;

  dataSource: Array<any>;
  series: Array<any>;
  options: Options = {};

  constructor(private dataService: DataService) {
    this.handleResize = debounce(this.handleResize, 100).bind(this);
  }

  async ngOnInit() {
    const results: DruidResults = await this.dataService.getDruidData();

    /*
      Druid topN results look like this:
      [
        {
          "timestamp": "ts1",
          "result": [
            {"<dim>": d1, "<metric>": mv1},
            {"<dim>": d2, "<metric>": mv2}
          ]
        },
        {
          "timestamp": "ts2",
          "result": [
            {"<dim>": d1, "<metric>": mv3},
            {"<dim>": d2, "<metric>": mv4}
          ]
        },
        ...
      ]
    */
    const dimensions = 'monitor_id';
    const metrics = [ 'SUM(es_sum)', 'MAX(response_time_max_max)' ];

    const data = results.map(result => {
      const point = {
        timestamp: new Date(result.timestamp)
      };

      metrics.forEach(metric => {
        point[metric] = result.result[0] ? result.result[0][metric] : null;
      });

      return point;
    });

    this.dataSource = data;
    this.series = metrics.map(metric => ({
      valueField: metric,
      name: metric
    }));

    this.options = {
      palette: 'Soft Pastel',
      argumentAxis: {
        argumentType: 'datetime',
        discreteAxisDivisionMode: 'crossLabels',
        valueMarginsEnabled: false,
        grid: {
          visible: true
        },
        label: {
          overlappingBehavior: 'stagger',
          format: 'dd/MM/yyyy HH:mm:ss'
        },
        visualRange: {
          length: {
            hours: 1
          }
        },
        visualRangeUpdateMode: 'shift'
      },
      commonSeriesSettings: {
        argumentField: 'timestamp',
        type: 'line',
        hoverMode: 'includePoints',
        hoverStyle: {
          width: 2
        },
        point: {
          hoverMode: 'onlyPoint',
          hoverStyle: {
            size: 5
          },
          size: 1
        },
        width: 1
      },
      legend: {
        verticalAlignment: 'top',
        horizontalAlignment: 'right',
        itemTextPosition: 'right'
      },
      crosshair: {
        enabled: true,
            color: '#949494',
            width: 3,
            dashStyle: 'dot',
            label: {
                visible: true,
                backgroundColor: '#949494',
                font: {
                  color: '#fff',
                  size: 12
                }
            }
      },
      tooltip: {
        enabled: true,
        zIndex: 10,
        customizeTooltip: this.customizeTooltip
      },
      export: {
        enabled: false,
        printingEnabled: true
      },
      zoomAndPan: {
        argumentAxis: 'both',
        dragToZoom: true,
        allowMouseWheel: true,
        panKey: 'shift'
      },
      scrollBar: {
        visible: true
      },
      loadingIndicator: {
        show: true
      }
    };
  }

  ngAfterViewInit() {
    this.render();
  }

  handleResize(event: ResizedEvent) {
    if (this.chart && this.chart.instance) {
      this.chart.instance.option('size', { height: event.newHeight, width: event.newWidth });
    }
  }

  render() {
    if (this.chart && this.chart.instance) {
      this.chart.instance.render();
    }
  }

  customizeTooltip = (pointInfo: any) => {
    return {
      text: pointInfo.seriesName + ': ' + pointInfo.point.value
    };
  }

  onLegendClick(event) {
    const series = event.target;
    if (series.isVisible()) {
        series.hide();
    } else {
        series.show();
    }
  }
}
