import { AfterViewInit, Component, OnDestroy, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ResizedEvent } from 'angular-resize-event';
import { DxChartComponent, DxContextMenuComponent } from 'devextreme-angular';
import DevExpress from 'devextreme/bundles/dx.all';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import { TestAgent } from '../test-agents/types';
import { Graph5Service } from './graph5.service';

@Component({
  selector: 'app-graph5',
  templateUrl: './graph5.component.html',
  styleUrls: ['./graph5.component.scss']
})
export class Graph5Component implements AfterViewInit, OnDestroy {
  @ViewChild(DxChartComponent) chart: DxChartComponent;
  @ViewChild(DxContextMenuComponent) contextMenu: DxContextMenuComponent;

  dataSource: DataSource;
  drillDownPoint: DevExpress.viz.basePointObject;
  contextMenuItems: any = [];

  constructor(private graph5Service: Graph5Service, private router: Router) {
    // this.handleResize = debounce(this.handleResize, 10).bind(this);
    this.dataSource = new DataSource({
      store: new ArrayStore({
        key: 'id',
        data: graph5Service.getTestAgents()
      }),
      sort: (ta: TestAgent) => ta.es
    });
  }

  ngAfterViewInit() {
    const targets = document.querySelectorAll('.clickable-bars .dxc-series rect');
    targets.forEach(target => target.addEventListener('contextmenu', this.showContextMenu));
  }

  handleResize(event: ResizedEvent) {
    if (this.chart && this.chart.instance) {
      this.chart.instance.option('size', { height: event.newHeight, width: event.newWidth });
    }
  }

  render() {
    if (this.chart && this.chart.instance) {
      this.chart.instance.render();
    }
  }

  onPointClick = (event: { target: { data: TestAgent; }; }) => {
    // TODO: Make a DrillDownService for handling drill downs.
    this.router.navigate(['/dashboard'], { queryParams: { id: 'testagent', ta: event.target.data.id } });
  }

  onContextMenuItemClick = (event) => {
    if (!this.drillDownPoint) {
      // log entry
      return;
    }

    // TODO: Make a DrillDownService for handling drill downs.
    this.router.navigate(['/dashboard'], { queryParams: { id: 'testagent', ta: this.drillDownPoint.data.id } });
  }

  getSeriesPointForDrillDown = (event: MouseEvent): DevExpress.viz.basePointObject => {
    const points = this.chart.instance.getSeriesByPos(0).getAllPoints();

    const candidatePoints = points.filter(point => point.isHovered());

    if (candidatePoints.length !== 1) {
      // console.warn("no series point or multiple series points were found")
      return null;
    }

    return candidatePoints[0];
  }

  showContextMenu = (event: MouseEvent) => {
    event.preventDefault();

    this.drillDownPoint = this.getSeriesPointForDrillDown(event);

    if (!this.drillDownPoint) {
      // log entry
      return;
    }

    this.contextMenuItems = [{
      text: this.drillDownPoint.originalArgument + ' details',
      icon: 'fields'
    }, {
      text: this.drillDownPoint.originalArgument + ' details for last',
      icon: 'clock',
      items: [{
        text: '15 mins'
      }, {
        text: '30 mins'
      }, {
        text: '1 hour'
      }, {
        text: '6 hours'
      }, {
        text: 'day'
      }]
    }];

    this.contextMenu.instance.option('position', { of: window, at: 'top left', my: 'top left', offset: event.pageX + ' ' + event.pageY });
    this.contextMenu.instance.show();
  }

  ngOnDestroy() {
    const targets = document.querySelectorAll('.clickable-bars .dxc-series rect');
    targets.forEach(target => target.removeEventListener('contextmenu', this.showContextMenu));
  }
}
