import { Injectable } from '@angular/core';
import { TestAgent } from '../test-agents/types';

@Injectable({
  providedIn: 'root'
})
export class Graph5Service {

  getTestAgents = (): Array<TestAgent> => {
    const data = [];
      // const now = Date.now();
    for (let index = 0; index < 5; index++) {
      data.push({
        id: index,
        name: 'Test Agent ' + index,
        es: Math.random(),
        sla: 100
      });
    }

    return data;
  }
}
