import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CompactType, DisplayGrid, GridsterComponent, GridsterConfig, GridType } from 'angular-gridster2';
import { Subscription } from 'rxjs';
import { GridService } from './grid.service';
import { DashboardItem, DashboardQueryParameters } from './types';

@Component({
  selector: 'app-grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.scss']
})
export class GridComponent implements OnDestroy, OnInit {
  @ViewChild(GridsterComponent) gridsterComponent: GridsterComponent;

  exportSub: Subscription;

  items: Array<DashboardItem>;
  options: GridsterConfig;

  queryParams: DashboardQueryParameters;

  readonly defaultQueryParams: DashboardQueryParameters = {
    id: 'default'
  };

  constructor(
    private gridService: GridService,
    private route: ActivatedRoute
  ) {
    this.options = {
      compactType: CompactType.CompactUp,
      displayGrid: DisplayGrid.None,
      draggable: {
        ignoreContent: true,
        enabled: true
      },
      fixedRowHeight: 100,
      gridType: GridType.VerticalFixed,
      margin: 8,
      maxCols: 8,
      maxRows: 24,
      minCols: 1,
      pushItems: true,
      pushResizeItems: true,
      resizable: { enabled: true }
    };
  }

  static itemChange(item, itemComponent) {
    console.log('itemChanged', item, itemComponent);
  }

  static itemResize(item, itemComponent) {
    console.log('itemResized', item, itemComponent);
  }

  ngOnInit() {
    this.route.queryParams.subscribe((params: DashboardQueryParameters) => this.setup(params));

    this.exportSub = this.gridService.exportEvent.subscribe(() => {
      this.export();
    });
  }

  setup(params: DashboardQueryParameters) {
    this.queryParams = { ...this.defaultQueryParams, ...params };

    this.items = this.gridService.getDashboard(this.queryParams.id);
  }

  changedOptions() {
    this.options.api.optionsChanged();
  }

  handleLockClick(itemIndex: number) {
    const item = this.items[itemIndex];
    const newLockedValue = item.locked = !item.locked;
    item.options.dragEnabled = !newLockedValue;
    item.options.resizeEnabled = !newLockedValue;
    this.gridsterComponent.optionsChanged();
  }

  removeItem(item) {
    this.items.splice(this.items.indexOf(item), 1);
  }

  export() {
    console.log('export event fired');
  }

  ngOnDestroy() {
    this.exportSub.unsubscribe();
  }
}
