import { ResizedEvent } from 'angular-resize-event';
import * as d3 from 'd3';

import {
    AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild
} from '@angular/core';

interface DataSet {
  y: number;
}

@Component({
  selector: 'app-graph1',
  templateUrl: './graph1.component.html',
  styleUrls: ['./graph1.component.scss']
})
export class Graph1Component implements AfterViewInit {
  @Input() graphsNum: number;

  colors: Array<string>;
  dataSets: Array<Array<DataSet>>;
  itemsNum: number;

  constructor(private elementRef: ElementRef) {
    this.itemsNum = 100;

    this.colors = [];
    this.dataSets = [];
    for (let i = 0; i < (this.graphsNum || 5); i++) {
      this.dataSets.push(d3.range(this.itemsNum).map((d) => ({
        y: d3.randomNormal(60 + 15 * i)()
      })));
      this.colors.push(
        `#${Math.floor(Math.random() * 16777215).toString(16)}`
      )
    }
  }

  ngAfterViewInit() {
    this.buildGraph(0, 0);
  }

  handleResize(event: ResizedEvent) {
    d3.select(this.elementRef.nativeElement)
      .selectAll('.container')
      .selectAll('.graph')
      .remove();
    this.buildGraph(event.newHeight, event.newWidth);
  }

  buildGraph(newHeight: number, newWidth: number) {
    const margin = {
      bottom: 20,
      left: 25,
      right: 5,
      top: 15,
    };
    const height = newHeight - margin.top - margin.bottom;
    const width = newWidth - margin.left - margin.right;

    const xScale = d3.scaleLinear()
      .domain([0, this.itemsNum - 1])
      .range([0, width]);

    const yScale = d3.scaleLinear()
      .domain([0, 150])
      .range([height, 0]);

    const line = d3.line()
      .x((d, i) => {
        return xScale(i);
      })
      .y((d) => {
        return yScale(d.y);
      })
      .curve(d3.curveMonotoneX);

    const svg = d3.select(this.elementRef.nativeElement)
      .select('.container')
      .append('svg')
      .attr('class', 'graph')
      .style('width', width + margin.left + margin.right)
      .style('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`);

    svg.append('g')
      .attr('transform', `translate(0, ${height})`)
      .call(d3.axisBottom(xScale).ticks(5));

    svg.append('g')
      .call(d3.axisLeft(yScale).ticks(3));

    this.dataSets.forEach((dataSet: Array<DataSet>, index: number) => {
      svg.append('path')
        .datum(dataSet)
        .style('fill', 'none')
        .style('stroke-width', 1)
        .style('stroke', this.colors[index])
        .attr('d', line);
    }, this);

    svg.append('g')
      .attr('transform', `translate(0, ${height})`)
      .style('stroke', 'lightgrey')
      .style('stroke-opacity', 0.15)
      .call(d3.axisBottom(xScale)
        .tickSize(-height)
        .tickFormat('')
        .ticks(5)
      );
  }
}
