import { Injectable } from '@angular/core';

import { TestAgent } from './types';

@Injectable({
  providedIn: 'root'
})
export class TestAgentsService {

  getTestAgents = (): Array<TestAgent> => {
    const data = [];
      // const now = Date.now();
    for (let index = 0; index < 50; index++) {
      data.push({
        id: index,
        name: 'Test Agent ' + index,
        es: Math.floor(Math.random() * 99),
        sla: 100
      });
    }
    return data;
  }
}
