import { ResizedEvent } from 'angular-resize-event';
import * as d3 from 'd3';

import {
    AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild
} from '@angular/core';

interface DataSet {
  y: number;
}

@Component({
  selector: 'app-graph2',
  templateUrl: './graph2.component.html',
  styleUrls: ['./graph2.component.scss']
})
export class Graph2Component implements AfterViewInit {
  @Input() graphsNum: number;

  colors: Array<{ stroke: string, fill: string }>;
  dataSets: Array<Array<DataSet>>;
  itemsNum: number;

  constructor(private elementRef: ElementRef) {
    this.itemsNum = 50;

    this.colors = [
      {
        stroke: '#589038',
        fill: '#c2dfb2',
      },
      {
        stroke: '#3c8b9c',
        fill: '#cee5ea',
      },
    ];

    this.dataSets = [];
    for (let i = 0; i < (this.graphsNum || 2); i++) {
      this.dataSets.push(d3.range(this.itemsNum).map((d) => ({
        y: d3.randomNormal(80 - 25 * i, 5)()
      })));
    }
  }

  ngAfterViewInit() {
    this.buildGraph(0, 0);
  }

  handleResize(event: ResizedEvent) {
    d3.select(this.elementRef.nativeElement)
      .selectAll('.container')
      .selectAll('.graph')
      .remove();
    this.buildGraph(event.newHeight, event.newWidth);
  }

  buildGraph(newHeight: number, newWidth: number) {
    const margin = {
      bottom: 20,
      left: 25,
      right: 5,
      top: 15,
    };
    const height = newHeight - margin.top - margin.bottom;
    const width = newWidth - margin.left - margin.right;

    const xRange = d3.scaleLinear()
      .domain([0, this.itemsNum - 1])
      .range([0, width]);

    const yRange = d3.scaleLinear()
      .domain([0, 120])
      .range([height, 0]);

    const area = d3.area()
      .x((d, i) => {
        return xRange(i);
      })
      .y1((d) => {
        return yRange(d.y);
      })
      .y0(yRange(0.1))
      .curve(d3.curveMonotoneX);

    const svg = d3.select(this.elementRef.nativeElement)
      .select('.container')
      .append('svg')
      .attr('class', 'graph')
      .style('width', width + margin.left + margin.right)
      .style('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`);

    svg.append('g')
      .attr('transform', `translate(0, ${height})`)
      .call(d3.axisBottom(xRange));

    svg.append('g')
      .call(d3.axisLeft(yRange).ticks(5));

    this.dataSets.forEach((dataSet: Array<DataSet>, index: number) => {
      svg.append('path')
        .datum(dataSet)
        .style('stroke', this.colors[index].stroke)
        .style('stroke-width', 1)
        .style('fill', this.colors[index].fill)
        .attr('d', area);
    }, this);

    svg.append('g')
      .style('stroke', 'lightgrey')
      .style('stroke-opacity', 0.15)
      .call(
        d3.axisLeft(yRange)
          .tickSize(-width)
          .tickFormat('')
          .ticks(5)
      );

    svg.append('g')
      .attr('transform', `translate(0, ${height})`)
      .style('stroke', 'lightgrey')
      .style('stroke-opacity', 0.15)
      .call(d3.axisBottom(xRange)
        .tickSize(-height)
        .tickFormat('')
        .ticks(10)
      );
  }
}
