import { ResizedEvent } from 'angular-resize-event';
import * as d3 from 'd3';

import {
    AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild
} from '@angular/core';

interface DataSet {
  y: number;
}

@Component({
  selector: 'app-graph3',
  templateUrl: './graph3.component.html',
  styleUrls: ['./graph3.component.scss']
})
export class Graph3Component implements AfterViewInit {
  constructor(private elementRef: ElementRef) { }

  ngAfterViewInit() {
    this.buildGraph(0, 0);
  }

  handleResize(event: ResizedEvent) {
    d3.select(this.elementRef.nativeElement)
      .selectAll('.container')
      .selectAll('.graph')
      .remove();
    this.buildGraph(event.newHeight, event.newWidth);
  }

  buildGraph(newHeight: number, newWidth: number) {
    const margin = {
      bottom: 20,
      left: 25,
      right: 5,
      top: 15,
    };
    const height = newHeight - margin.top - margin.bottom;
    const width = newWidth - margin.left - margin.right;

    const svg = d3.select(this.elementRef.nativeElement)
      .select('.container')
      .append('svg')
      .attr('class', 'graph')
      .style('width', width)
      .style('height', height)
      .append('g')
      .attr('transform', `translate(${Math.round(width / 2)}, ${Math.round(height / 2)})`);

    const radius = Math.min(width, height) / 2;

    const data = { a: 9, b: 20, c: 30, d: 8, e: 12, f: 3, g: 7, h: 14 };

    const color = d3.scaleOrdinal()
      .domain(['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h'])
      .range(d3.schemeDark2);

    const pie = d3.pie()
      .sort(null)
      .value((d) => (d.value));
    const dataReady = pie(d3.entries(data));

    const arc = d3.arc()
      .innerRadius(radius * 0.5)
      .outerRadius(radius * 0.8);

    const outerArc = d3.arc()
      .innerRadius(radius * 0.9)
      .outerRadius(radius * 0.9);

    svg
      .selectAll('allSlices')
      .data(dataReady)
      .enter()
      .append('path')
      .attr('d', arc)
      .attr('fill', (d) => (color(d.data.key)))
      .attr('stroke', 'white')
      .style('stroke-width', '2px')
      .style('opacity', 0.7);

    svg
      .selectAll('allPolylines')
      .data(dataReady)
      .enter()
      .append('polyline')
      .attr('stroke', 'black')
      .style('fill', 'none')
      .attr('stroke-width', 1)
      .attr('points', (d) => {
        const posA = arc.centroid(d)
        const posB = outerArc.centroid(d)
        const posC = outerArc.centroid(d);
        const midangle = d.startAngle + (d.endAngle - d.startAngle) / 2;
        posC[0] = radius * 0.95 * (midangle < Math.PI ? 1 : -1);
        return [posA, posB, posC];
      });

    svg
      .selectAll('allLabels')
      .data(dataReady)
      .enter()
      .append('text')
      .text((d) => (d.data.key))
      .attr('transform', (d) => {
        const pos = outerArc.centroid(d);
        const midangle = d.startAngle + (d.endAngle - d.startAngle) / 2;
        pos[0] = radius * 0.99 * (midangle < Math.PI ? 1 : -1);
        return `translate(${pos})`;
      })
      .style('text-anchor', (d) => {
        const midangle = d.startAngle + (d.endAngle - d.startAngle) / 2;
        return (midangle < Math.PI ? 'start' : 'end');
      });
  }
}
